.rr-container {
    width: 100%;
    background-color: white;
    padding-top: 20px;
}

.rr-box {
    width: 90%;
    margin: 40px auto;
    font-family: lemon-milk;
    border-radius: 0 5px 5px 5px;
}


.rr-box h2 {
    border-left: 15px solid #005987;
    text-align: center;
    background-color: #f4d90c;
    color: #005987;
    border-radius: 0 5px 5px 0;
    font-size: 28px;
}

.rr-box h3 {
    width: 70%;
    margin: 30px auto;
    padding: 10px 0;
    font-family: 'Barlow', 'san-serif';
    font-style: italic;
    font-size: 22px;
    font-weight: 500;
    text-align: center;
    background-color: rgba(244, 217, 12, 0.2);
    color: rgb(31, 31, 31);
    border-radius: 5px;
}

.rr-box h4 {
    font-family: 'Barlow', 'san-serif';
    margin: 10px 5px;
    font-weight: 300;
    font-size: 16px;
}

@media only screen and (min-width: 768px){
    .rr-container{
        width: 100%;
        padding: 20px 0;
        box-sizing: border-box;
    }

    .rr-box {
        width: 80%;
        margin: 0 auto;
    }

    .rr-box h2 {
        width: 100%;
        margin: 50px auto;
        text-align: left;
        padding-left: 40px;
    }

    .rr-box h3 {
        font-size: 28px;
        margin-bottom: 100px;
    }
    
    .rr-box h4 {
        font-size: 18px;
    }

}

@media only screen and (min-width: 1000px){
    .rr-container{
        padding: 20px 0;
    }

    .rr-box {
        width: 70%;
    }

    .rr-box h3 {
        font-size: 32px;
    }
    
    .rr-box h4 {
        font-size: 22px;
    }

}

@media only screen and (min-width: 1200px){

    .rr-box {
        width: 60%;
    }
}

