@font-face {
    font-family: lemon-milk;
    src: url(../../fonts/LEMONMILK-Regular.otf);
  }

footer {
    /* background-color: rgb(240, 240, 240); */
    background-color: rgb(31, 31, 31);
    /* background-color: #071e3d; */
    color: #999999;
    border-top: 2px solid #f4d90c;
    border-bottom: 2px solid rgb(31, 31, 31);
    /* margin-top: 50px; */
}

.newsletter-container {
    width: 100%;
    background-color: rgb(31, 31, 31);
    /* background-color: #005987; */
    color: #f4d90c;
    font-family: lemon-milk;
    text-align: center;
    padding: 10px 0;
}

.newsletter-form {
    width: 80%;
    margin: 10px auto;
    display: flex;
    flex-direction: column;
}

.newsletter-form input {
    border: none;
    outline: none;
    border-radius: 5px;
    font-size: 20px;
    margin: 10px 0;
    padding: 5px;
    font-family: 'Barlow', 'san-serif';
    color: #005987;
}

.newsletter-container button {
    border: none;
    background-color: #f4d90c;
    color: black;
    font-family: lemon-milk;
    font-size: 24px;
    padding: 5px 10px;
}

footer div h5 {
    width: 80%;
    margin: 0 auto;
    padding-top: 20px;
    text-align: center;
    font-family: lemon-milk;
    font-weight: 300;
    color: #f4d90c;
}

.gov-agencies-container {
    width: 100%;
    margin: 20px 0;
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    /* background-color: white; */
    background-color: rgb(31, 31, 31);
}

.gov-agencies-container img {
    width: 40%;
    margin: 5px auto;
    border: 1px solid #eeeeee;
    height: 10vh;
    border-radius: 5px;
}

.footer-charity-info {
    font-size: 18px;
    width: 90%;
    margin: 20px auto;
    background-color: rgb(31, 31, 31);
    color: #f4d90c;
    font-family: 'Barlow', 'san-serif';
    display: flex;
    flex-direction: row;
    justify-content: space-evenly;
}


.footer-contact-container {
    width: 90%;
    margin-top: 10px;
    margin: 0 auto;
    background-color: rgb(31, 31, 31);
    color: #f4d90c;
    font-family: 'Barlow', 'san-serif';
    display: flex;
    flex-direction: column;
    justify-content: space-evenly;
}

.footer-a {
    width: 100%;
    display: flex;
    flex-direction: row;
    justify-content: flex-start;
    align-items: flex-start;
}

.footer-a-1 {
    width: 100%;
    display: flex;
    flex-direction: column;
    justify-content: center;
}

.footer-a-1 h4,
.footer-a-1 h3 {
    margin-top: 5px;
}

.footer-a-1 h4 a {
    text-decoration: none;
    color: #f4d90c;
    font-family: 'Barlow', sans-serif;
}

.footer-a-1 h4 a:hover {
    font-weight: 500;
    color: #f4d90c;
}

.footer-b {
    width: 100%;
    display: flex;
    flex-direction: row;
    justify-content: space-evenly;
    margin: 20px 0;
}

.footer-b a {
    width: 20%;
    max-width: 100px;
}

.footer-b a img {
    width: 100%;
    border-radius: 40px;
    box-shadow: 2px 2px 15px rgba(0, 0, 0, 0.8);
    opacity: 1;
    transition: opacity 0.3s ease-in-out;
}

.footer-b img:hover {
    opacity: 0.8;
}

@media only screen and (min-width: 450px) {
    .footer-a-1 {
        margin: 0 5%;
    }

    .footer-b a img {
        border-radius: 80px;
    }

}

@media only screen and (min-width: 768px) {

    .footer-contact-container {
        flex-direction: row;
        width: 70%;
        margin: 0 auto;
    }

    .footer-a {
        width: 80%;
        margin-top: 30px;
    }

    .footer-a-1 {
        width: 40%;
    }

    .footer-b {
        width: 20%;
        flex-direction: column;
        align-items: center;
    }

    .footer-b a {
        width: 40%;
        margin-top: 10px;
    }

    .footer-a-1 h4,
    .footer-a-1 h3 {
        font-size: 18px;
    }

    .newsletter-container {
        font-size: 2em;
    }

    .newsletter-form {
        width: 700px;
    }

    .newsletter-form input {
        margin: 20px 0;
        font-size: 26px;
    }

    .gov-agencies-container {
        width: 700px;
        margin: 10px auto;
    }

}

@media only screen and (min-width: 1200px){
  
    .footer-charity-info {
      font-size: 22px;
      width: 900px;
      margin: 30px auto;
    }
  
    .footer-contact-container {
      width: 800px; 
    }
  
    .footer-b a {
      width: 40%;
    }
  }