.background-slider {
    /* background-color: rgba(244, 217, 12, 0.8); */
    /* background-image: linear-gradient(to top left, rgba(244, 217, 12, 0.5), rgb(244, 217, 12)); */
    position: relative;
    width: 90%;
    height: calc(100vh - 230px);
    margin: 0 auto;
    margin-top: 120px;
    overflow: hidden;
    border-radius: 4px;
    opacity: 0;
    animation-name: lightsOn2;
    animation-duration: 1s;
    animation-delay: 1.5s;
    animation-fill-mode: forwards;
    border-radius: 4px;
    z-index: 2;
}

@keyframes lightsOn2 {
    from {
        opacity: 0;
    }

    to {
        opacity: 1;
    }
}

.slick-slider {
    width: 100%;
    height: 90%;
    margin: auto;
    display: flex;
    align-items: center;
    justify-content: center;
}

.slick-list {
    height: 90%;
}

.slick-track {
    height: 100%;
}

.background-text {
    display: flex !important;
    flex-direction: column;
    justify-content: space-evenly;
    align-items: center;
    text-align: center;
}

.mission-statement-heading {
    width: 100%;
    background-color: rgb(244, 217, 12);
    text-align: center;
    font-family: lemon-milk;
    border-radius: 5px;
    font-size: 3.25vh;
    font-weight: 800;
    color: black;
    margin: 10px 0;
}

.mission-statement-body {
    background-color: rgba(244, 217, 12, 0.8);
    width: 100%;
    margin: 0 auto;
    padding: 20px 10px;
    box-sizing: border-box;
    text-align: center;
    /* font-size: 20px; */
    font-size: 2.75vh;
    color: black;
    font-family: 'Barlow', 'san-serif';
    font-weight: 600;
    border-radius: 20px;
}

.slick-dots {
    width: 100%;
    height: 30px;
    margin: auto;
    display: flex;
    flex-direction: row;
    justify-content: space-evenly;
    list-style: none;
}

.slick-dots li {
    list-style-type: none;
}

.slick-dots li button {
    border: none;
    font-size: 0;
    width: 15px;
    height: 15px;
    border-radius: 15px;
    background-color: rgba(200, 200, 200, 0.5);
    background-color: yellow;
    font-size: 0;
    line-height: 0;
    display: block;
    width: 20px;
    height: 20px;
    padding: 5px;
    cursor: pointer;
    color: black;
    border: 0;
    outline: none;
    background: transparent;
}

.slick-dots li button:hover,
.slick-dots li button:focus {
    outline: none;
}

.slick-dots li button:hover:before,
.slick-dots li button:focus:before {
    opacity: 1;
}

.slick-dots li button:before {
    font-family: 'slick';
    font-size: 15px;
    line-height: 20px;
    position: absolute;
    top: 0;
    left: 0;
    width: 20px;
    height: 20px;
    content: '•';
    text-align: center;
    opacity: .25;
    color: white;
    color: yellow;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
}

.slick-dots li.slick-active button:before {
    opacity: .75;
    color: white;
    color: yellow;
}

