    width: 100%;
    height: 100%;
    margin: auto;
}

.overlay-video {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100vh;
    background-color: rgba(0, 89, 135, 0.95);
    overflow: hidden;
    display: block;
    z-index: 5;
    opacity: 0;
    animation: overlayTransitionAnim 0.5s ease-in-out forwards;
}

@keyframes overlayTransitionAnim {
    from {
        opacity: 0;
    }

    to {
        opacity: 1;
    }
}

